@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

body {
  background: white;
  color: black;
  font-family: "Roboto", sans-serif;
  letter-spacing: 0.05em;
  margin: 0;
}

#main--content {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.div--app{
  width: 80%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.div--app__search {
  width: 60%;
  max-width: 800px;
  min-width: 275px;
  background: white;
  display: flex;
  flex-direction: column;
  border-radius: 15px;
  transition: width 0.3s;
  border: 1px solid #c7caca;
  margin-top: 2rem;
}

.form {
  text-align: right;
  display: flex;
  justify-content: space-between;
  padding: 1rem;
}

.form__input {
  flex-grow: 2;
  font-size: 1.5rem;
  border: none;
}

.form__input:focus {
  outline: none;
}

input:focus::-webkit-input-placeholder {
    color: transparent;
}

.form__submit-btn {
  height: 2rem;
  width: 2em;
  opacity: 0.2;
  background-color: transparent;
  border: 0;
  background-repeat: no-repeat;
  background-size: 100%;
  background-image: url("data:image/png;base64,");
}
.form__submit-btn:hover {
  outline: none;
  opacity: 0.4;
  cursor: pointer;
}
.form__submit-btn:focus {
  outline: none;
  opacity: 0.6;
}

.div--options {
  width: 100%;
  transition: width 0.2s;
  border-radius: 15px;
}

.div--options ul {
  list-style: none;
}

.div--options ul li {
  color: black;
  font-size: 1rem;
  width: 100%;
  margin: 1rem 0rem; 
}

.div--options ul li:hover {
  color: #00b4cc;
  cursor: pointer;
  font-size: 1rem;
  transition: 0.3s all;
}

.div--options ul li.li--active {
  font-size: 1rem;
  color: #00b4cc;
}

.div--options em {
  color:grey;
  font-size: 1rem;
  margin: 1rem 1rem;
}

.recipe {
  width: 40%;
  max-width: 600px;
  position: absolute;
  margin: 8rem;
  z-index: -1;
}

.recipe em {
  color: grey;
}

@media screen and (max-width: 825px) {
  .div--app{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .div--app__search {
    width: 90%;
  }

  .form {
    display: flex;
    justify-content: space-between;
    text-align: right;
    font-size: 1rem;
    padding: 0.5rem;
  }

  .div--options {
    width: 90%;
    min-width: 220px;
  }
  
  .form:focus {
    border: 1px solid #9b9c9c;
  }

  .form__input {
    flex-grow: 2;
    border: none;
  }

  .form__submit-btn {
    height: 1.5rem;
    width: 1.5em;
    margin-top: 5px;
    opacity: 0.2;
  }

  .recipe {
    width: 70%;
    font-size: 1rem;
  }
}

@media only screen and (max-width: 425px) {
  .div--app{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .div--app__search {
    width: 90%;
  }

  .form {
    display: flex;
    justify-content: space-between;
    text-align: right;
  }

  .form__input {
    flex-grow: 2;
    font-size: 1rem;
    border: none;
  }

  .form__submit-btn {
    height: 1rem;
    width: 1em;
    opacity: 0.2;
  }

  .recipe {
    width: 70%;
    font-size: 0.8rem;
  }
}

@media only screen and (max-width: 360px) {
  .div--app {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .div--app__search {
    width: 90%;
  }

  .form {
    display: flex;
    justify-content: space-between;
    text-align: right;
    transition: width 0.3s;
  }

  .form__input {
    flex-grow: 2;
    font-size: 1rem;
    border: none;
  }

  .form__submit-btn {
    height: 1rem;
    width: 1em;
    opacity: 0.2;
  }

  .recipe {
    width: 80%;
    font-size: 0.8rem;
  }
}